body {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
  padding: 10px;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  max-width: 100%;
  margin: 0 auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

h1, h2, h3 {
  text-align: center;
  font-size: 24px;
  margin: 20px 0;
  transition: color 0.3s ease;
}

.menu-section {
  margin: 10px 0;
  flex-grow: 1;
  animation: fadeIn 1s ease-in-out;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.item-info {
  display: flex;
  flex-direction: column;
  flex: 2;
}

.item-name {
  font-size: 18px;
  margin-bottom: 0px;
}

.item-price {
  font-size: 16px;
  color: #bbbbbb;
}

.item-select {
  flex: 1;
  padding: 5px;
  font-size: 18px;
  margin-right: 10px;
  text-align: center;
}

.item-total {
  flex: 1;
  font-size: 18px;
  text-align: right;
}

.totals {
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 100%;
  background-color: #121212;
  z-index: 1000;
  padding: 0px;
  border-bottom: 1px solid #333;
}

.section-total{
  font-size: 18px;
  text-align: left;
  margin: 5px 0;
}

.grand-total {
  color: aqua;
  font-size: 24px;
  text-align: left;
  margin: 5px 0;
}

.clear-button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  margin-top: 20px;
  display: block;
  width: 100%;
  text-align: center;
  border-radius: 5px;
  position: fixed;
  bottom: 5%;
  right: 5%;
  max-width: max-content;
  z-index: 1000;
}

.clear-button:hover {
  background-color: #d32f2f;
}

.menu-section {
  margin-top: 0px; /* To avoid overlap with the fixed totals section */
  margin-bottom: 15px; /* To avoid overlap with the fixed clear button */
}

.menu-section-bottom {
  margin-top: 0px; /* To avoid overlap with the fixed totals section */
  margin-bottom: 150px; /* To avoid overlap with the fixed clear button */
}

.footer {
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  max-width: 100%;
  background-color: #121212;
  z-index: 1000;
  padding: 10px;
  border-top: 1px solid #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  }

  body > .App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

  .menu-section {
    margin-top: 10px;
    margin-bottom: 20px;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes countUp {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }